@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.links line {
  stroke: rgb(65, 182, 174);
  stroke-opacity: 0.6;
}

.nodes circle {
  stroke: #fff;
  stroke-width: 1.5px;
  border: solid, black, 2em;
}

.container {
  display: grid;
  grid-template-columns: [column1-start] 1100px [column1-end column2-start] auto [column2-end];
  grid-template-areas: "svg | Recipe-box";
}

.graph {
  grid-column-start: column1;
  grid-column-end: column2;
}


.Recipe-search {
  grid-column-start: column2;
  grid-column-end: end;
}
.submit-btn {
  flex: 0.5 1;
  margin: 1rem;
  width: 50%;
  background-image: linear-gradient(262deg, #ff7854, #fd267d);
  -webkit-transition: 0.25s;
  transition: 0.25s;
  padding: 10px 20px;
  text-align: center;
  font-size: 1em;
  text-decoration: none;
  display: inline-block;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 16px;
}

/* .submit-btn:hover {
  background-color:#fd267d;
} */

.text {
  font-size: 2em;
}

.form{
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  left: 20px;
}

input {
  width: 90%;
  height: 100%;
  padding: 10px 20px;
  box-sizing: border-box;
  background-color: white;
  font-size: 1em;
}

input:focus {
  background-color: white;
}


.search-box {
  height: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
}
.App {
  font-family: 'Montserrat', sans-serif;
}

.App-body {
  height: auto;
  min-height: 100%;
}

.App-header {
  text-align: center;
  background-image: linear-gradient(262deg, #ff7854, #fd267d);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(50px + 2vmin);
  font-weight: bolder;
  color: white;
  height: 125px;
  padding: 70px;
}

.App-logo {
  height: 150px;
  position: absolute;
  top: 20px;
  left: 65px;
}

.App-brand {
  position: absolute;
  top: 80px;
  left: 45px;
  font-weight: 900;
}

.App-title {
  font-size: calc(130px + 2vmin);
  position: absolute;
  left: 225px;
  top: -85px;
}

.App-subtitle {
  font-size: 1em;
  position: absolute;
  right: 75px;
  top: 100px;
  color: black
}



